.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.wrap-slider {
  position: relative;
  max-width: 720px;
  margin: 0 auto;
}
.wrap-slider .slider {
  padding: 0;
}
.wrap-slider .slick-slide {
  height: 100vh;
  background-size: cover;
  text-align: center;
  background-position: center center;
}
.wrap-slider .slick-slide .content-slider {
  position: relative;
  top: 0;
   height: 100vh;
  background-size: cover;
  text-align: center;
  background-position: center center;
}

.wrap-slider .slick-slide .content-slider {
  top: 0;
  
}
.wrap-slider .slick-slide .content-slider img {
  display: block;
  margin: 0 auto 2em;
  width: 100%;
  height: 100%
}

.wrap-slider .slick-slide p {
  font-size: 1.75em;
  color: #fff;
}
.wrap-content-slider {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100%;
  pointer-events: none;
}

